.rainbowText {
    background: conic-gradient(#EB5757, #F2994A, #F2C94C, #6FCF97, #56CCF2, #BB6BD9);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

.page {
    width: 100%;
    float: left;
}

.e-content {
    padding: 0 8em;
}

#page-title {
    display: none;
}

#hero {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

#countdown {
    width: 60%;
}

#countdown>a {
    font-size: 1.25em;
}

#logo {
    width: 40%;
}

#timer {
    font-size: 2em;
    font-weight: bold;
}

#logos {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

#logos>img {
    width: 30vw;
}

#past-imgs {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

#past-imgs > div {
    display: flex;
    flex-direction: row;
}

#past-imgs > div > img {
    width: MAX(300px, 21vw);
    height: MAX(200px, 14vw);
    margin: 10px;
}

#cmts {
    display: grid;
    grid-template-columns: repeat(5, calc(80vw/6));
    grid-template-rows: repeat(4, calc(80vw/5));
    gap: 0.5em;
}

#cmts>div {
    width: calc(80vw/6);
    /* height: calc(80vw/6 + 5vw); */

    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1em;
    border-radius: 1em;
    background-color: gray;

}

#cmts>div>img {
    border-radius: 9999px;
    object-fit: cover;
    width: calc(80vw/10);
    height: calc(80vw/10);
}

#cmts>div>h6 {
    margin-top: 0.5em;
    font-size: 0.8em;
    text-align: center;
}

#cmts>div:hover #cmts>div>p {
    display: block;
}

@media only screen and (min-width: 769px) {
    .tooltip {
        position: relative;
    }

    .tooltip .tooltiptext {
        /* visibility: hidden; */
        opacity: 0;
        width: 9em;
        font-size: 0.6em;
        background-color: black;
        color: #fff;
        text-align: center;
        border-radius: 6px;
        padding: 5px 0;

        /* Position the tooltip */
        position: absolute;
        z-index: 1;

        top: 90%;
        left: 50%;
        margin-left: -4.5em;

    }

    .tooltip:hover .tooltiptext {
        /* visibility: visible; */
        opacity: 1;
    }

    .tooltip .tooltiptext::after {
        content: " ";
        position: absolute;
        bottom: 100%;
        /* At the top of the tooltip */
        left: 50%;
        margin-left: -0.75em;
        border-width: 0.75em;
        border-style: solid;
        border-color: transparent transparent black transparent;
    }
}

@media only screen and (max-width: 768px) {

    /* For mobile phones: */
    .e-content {
        padding: 0;
    }

    #hero {
        flex-direction: column-reverse;
    }

    #countdown,
    #logo {
        width: 80%;
    }

    #timer {
        font-size: 1.5em;
    }

    #logos {
        flex-direction: column;
    }

    #logos>img {
        width: 80%;
    }

    #cmts>div {
        width: 10em;
    }

    #cmts>div>img {
        width: 6em;
        height: 6em;
    }

    #cmts {
        display: inline-flex;
        flex-direction: row;
        justify-content: space-evenly;
        padding: 1em 0;
    }

    #cmts-wrapper {
        overflow-x: scroll;
    }

    #cmts-wrapper::-webkit-scrollbar {
        height: 8px;
    }

    #cmts-wrapper::-webkit-scrollbar-track {
        border-radius: 8px;
    }

    #cmts-wrapper::-webkit-scrollbar-thumb {
        background: gray;
        border-radius: 8px;
    }

    #cmts>div>span {
        margin-bottom: 0;
        display: block;
        font-size: 0.8em;
    }

}
